*{
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}
.header {
	background: #000;
	height: 200px;
}
.query-input {
	max-width: 320px;
	width: 100%;
	height: 40px;
	border-radius: 4px;
	box-shadow: none;
	border: 2px solid #8686fc;
}
select {
	height: 40px;
	border-radius: 4px;
	box-shadow: none;
	border: 2px solid #8686fc;
	margin-bottom: 20px;
}


